/**
 * Router
 * @class Router
 * @classdesc 路由
 */
var Router = /** @class */ (function () {
    function Router() {
        this.controllers = new Map();
    }
    /**
     * controller
     * @description 添加控制器
     * @param path
     * @param middleWare
     */
    Router.prototype.controller = function (path, middleWare) {
        this.controllers.set(path, function (ctx, next) {
            if (path === ctx.getRequest().getPathname()) {
                middleWare(ctx, next);
                return;
            }
            next && next();
        });
        return this;
    };
    /**
     * routers
     * @description 获取所有的中间件
     */
    Router.prototype.routers = function () {
        return Array.from(this.controllers.values());
    };
    return Router;
}());
export default Router;
